<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <h2>Calculate Trip Cost</h2>
    </div>
    <el-form
      ref="form"
      :model="form"
      label-width="120px"
      label-position="left">
      <el-form-item label="Start Time">
        <el-col :span="11">
          <el-date-picker
            type="date"
            placeholder="Pick a date"
            v-model="form.startDate"
            style="width: 100%;"
            @change='getTripCost'>
          </el-date-picker>
          <el-button 
            type="text"
            @click="resetTime('start')">
            Set To Current Date/Time
          </el-button>
        </el-col>
        <el-col 
          :span="11"
          :offset="1">
          <el-input
            class="time-input"
            type="time"
            v-model="form.startTime"
            style="width: 100%;"
            @change='getTripCost'
            prefix-icon="el-icon-time">
          </el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="End Time">
        <el-col :span="11">
          <el-date-picker
            type="date"
            placeholder="Pick a date"
            v-model="form.endDate"
            style="width: 100%;"
            @change='getTripCost'>
          </el-date-picker>
          <el-button 
            type="text"
            @click="resetTime('end')">
            Set To Current Date/Time
          </el-button>
        </el-col>
        <el-col 
          :span="11"
          :offset="1">
          <el-input
            class="time-input"
            type="time"
            v-model="form.endTime"
            style="width: 100%;"
            @change='getTripCost'
            prefix-icon="el-icon-time"
            controls="false">
          </el-input>
        </el-col>
      </el-form-item>
    </el-form>

    <el-divider></el-divider>

    <div id='cost-breakdown-header'>
      <h3>Breakdown</h3>
    </div>
    
    <el-descriptions class="margin-top" :column="1" size="medium" border>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-time"></i>
          Trip Duration
        </template>
        {{ 
          tripDuration.days + (tripDuration.days == 1 ? ' Day, ' : ' Days, ') + 
          tripDuration.hours + (tripDuration.hours == 1 ? ' Hour, ' : ' Hours, ') + 
          tripDuration.minutes + (tripDuration.minutes == 1 ? ' Minute' : ' Minutes') 
        }}
      </el-descriptions-item>
    </el-descriptions>
      
    <el-table
      :data="tableData"
      :show-header='showHeader'
      class="cost-table"
      :row-class-name="highlightTotals">
      <el-table-column
        prop="name">
      </el-table-column>
      <el-table-column
        prop="value"
        align="right">
      </el-table-column>
    </el-table>

  </el-card>
</template>

<script>
import { calculateTripCost, calculateTripDuration } from '../helpers/tripCalculationHelper';

export default {
  name: 'CalculatorForm',
  props: {
    rates: {
      minuteRate: Number,
      hourRate: Number,
      dayRate: Number,
      pvrtRate: Number,
      accessFee: Number
    },
    taxes: {
      gst: Number,
      pst: Number
    }
  },
  watch: { 
    rates: function() {
      this.totalCost = calculateTripCost(this.rates, this.taxes, this.form.startDate, this.form.startTime, this.form.endDate, this.form.endTime);
    }
  },
      
  data() {
    return {
      showHeader: false,

      dayDifference: 0,
      hourDifference: 0,
      minuteDifference: 0,

      form: {
        startDate: Date(),
        startTime: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }),
        endDate: Date(),
        endTime: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }),
      },

      totalCost: {
        tripCost: 0,
        taxOnTripCost: 0,
        pvrtCost: 0,
        taxOnPvrt: 0,
        accessFeeCost: 0,
        taxOnAccessFee: 0,
      }
    }
  },

  methods: {
    getTripCost() {
      this.totalCost = calculateTripCost(this.rates, this.taxes, this.form.startDate, this.form.startTime, this.form.endDate, this.form.endTime);
    },

    resetTime(dateCategory) {
      const currentDate = new Date();
      if (dateCategory == 'start') {
        this.form.startTime = currentDate.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
        this.form.startDate = currentDate
      } else {
        this.form.endTime = currentDate.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
        this.form.endDate = currentDate
      }
      this.totalCost = calculateTripCost(this.rates, this.taxes, this.form.startDate, this.form.startTime, this.form.endDate, this.form.endTime);
    },

    highlightTotals({rowIndex}) {
      if (rowIndex === 2) {
        return 'subtotal-row';
      } else if (rowIndex === 7) {
        return 'total-row';
      }
      return '';
    },
  },

  computed: {
    tripDuration: function() {
      return calculateTripDuration(this.form.startDate, this.form.startTime, this.form.endDate, this.form.endTime);
    },

    tableData: function() {
      let tableData = [{
          name: 'Trip Cost',
          value: this.totalCost.tripCost > 0 ? '$' + this.totalCost.tripCost.toFixed(2) : '$0.00'
        }, {
          name: 'Tax on trip cost',
          value: this.totalCost.tripCost > 0 ? '$' + this.totalCost.taxOnTripCost.toFixed(2) : '$0.00'
        }, {
          name: 'Subtotal',
          value: this.totalCost.tripCost > 0 ? '$' + (this.totalCost.tripCost + this.totalCost.taxOnTripCost).toFixed(2) : '$0.00'
        }, {
          name: 'PVRT',
          value: this.totalCost.tripCost > 0 ? '$' + this.totalCost.pvrtCost.toFixed(2) : '$0.00'
        }, {
          name: 'Tax on PVRT',
          value: this.totalCost.tripCost > 0 ? '$' + this.totalCost.taxOnPvrt.toFixed(2) : '$0.00'
        }, {
          name: 'Access Fee',
          value: this.totalCost.tripCost > 0 ? '$' + this.totalCost.accessFeeCost.toFixed(2) : '$0.00'
        },{
          name: 'Tax on Access Fee',
          value: this.totalCost.tripCost > 0 ? '$' + this.totalCost.taxOnAccessFee.toFixed(2) : '$0.00'
        }, {
          name: 'Total Cost',
          value: (
            this.totalCost.tripCost > 0 
            ? '$' + (
              this.totalCost.tripCost 
              + this.totalCost.taxOnTripCost 
              + this.totalCost.pvrtCost 
              + this.totalCost.taxOnPvrt 
              + this.totalCost.accessFeeCost 
              + this.totalCost.taxOnAccessFee).toFixed(2) 
            : '$0.00'
          )
      }]

      return tableData;
    }
  }
}
</script>

<style>
h2 {
  margin: 0;
}

#cost-breakdown-header {
  margin-bottom: 10px;
}

.time-input input{
  font-family: inherit;
}

.card-button {
  float: right;
  padding: 3px 0
}

.el-descriptions {
  margin-bottom: 15px
}

.el-table .total-row {
  font-weight: bold;
  background: #b9e79f;
}

.el-table .subtotal-row {
  font-weight: bold;
  background: rgb(231, 217, 189);
}

.reset-button {
  padding: 0;
}

</style>